export default ({
    props: {
        id: {
            type: undefined,
            default () { return null },
        },
    },
    data () {
        return {
            map: null,
            google: null,
            map_start: false,
            mapResizeTimer: null,
            centerMarker1: null,
            map_styles: [
                {
                    featureType: 'water',
                    elementType: 'geometry',
                    stylers: [
                        {
                            color: '#e9e9e9',
                        },
                        {
                            lightness: 17,
                        },
                    ],
                },
                {
                    featureType: 'landscape',
                    elementType: 'geometry',
                    stylers: [
                        {
                            color: '#f5f5f5',
                        },
                        {
                            lightness: 20,
                        },
                    ],
                },
                {
                    featureType: 'road.highway',
                    elementType: 'geometry.fill',
                    stylers: [
                        {
                            color: '#ffffff',
                        },
                        {
                            lightness: 17,
                        },
                    ],
                },
                {
                    featureType: 'road.highway',
                    elementType: 'geometry.stroke',
                    stylers: [
                        {
                            color: '#ffffff',
                        },
                        {
                            lightness: 29,
                        },
                        {
                            weight: 0.2,
                        },
                    ],
                },
                {
                    featureType: 'road.arterial',
                    elementType: 'geometry',
                    stylers: [
                        {
                            color: '#ffffff',
                        },
                        {
                            lightness: 18,
                        },
                    ],
                },
                {
                    featureType: 'road.local',
                    elementType: 'geometry',
                    stylers: [
                        {
                            color: '#ffffff',
                        },
                        {
                            lightness: 16,
                        },
                    ],
                },
                {
                    featureType: 'poi',
                    elementType: 'geometry',
                    stylers: [
                        {
                            color: '#f5f5f5',
                        },
                        {
                            lightness: 21,
                        },
                    ],
                },
                {
                    featureType: 'poi.park',
                    elementType: 'geometry',
                    stylers: [
                        {
                            color: '#dedede',
                        },
                        {
                            lightness: 21,
                        },
                    ],
                },
                {
                    elementType: 'labels.text.stroke',
                    stylers: [
                        {
                            visibility: 'on',
                        },
                        {
                            color: '#ffffff',
                        },
                        {
                            lightness: 16,
                        },
                    ],
                },
                {
                    elementType: 'labels.text.fill',
                    stylers: [
                        {
                            saturation: 36,
                        },
                        {
                            color: '#333333',
                        },
                        {
                            lightness: 40,
                        },
                    ],
                },
                {
                    elementType: 'labels.icon',
                    stylers: [
                        {
                            visibility: 'off',
                        },
                    ],
                },
                {
                    featureType: 'transit',
                    elementType: 'geometry',
                    stylers: [
                        {
                            color: '#f2f2f2',
                        },
                        {
                            lightness: 19,
                        },
                    ],
                },
                {
                    featureType: 'administrative',
                    elementType: 'geometry.fill',
                    stylers: [
                        {
                            color: '#fefefe',
                        },
                        {
                            lightness: 20,
                        },
                    ],
                },
                {
                    featureType: 'administrative',
                    elementType: 'geometry.stroke',
                    stylers: [
                        {
                            color: '#fefefe',
                        },
                        {
                            lightness: 17,
                        },
                        {
                            weight: 1.2,
                        },
                    ],
                },
            ],
            resizeFunc: null,
        }
    },
    mounted () {
        this.google = window.google
        this.start_map()
        this.resizeFunc = this.resize.bind(this)
        window.addEventListener('resize', this.resizeFunc)
    },
    beforeDestroy () {
        window.removeEventListener('resize', this.resizeFunc)
    },
    methods: {
        start_map () {
            this.map_start = true
            const lat = 25.041 + 0.05
            const lng = 121.5379
            const mapProp = {
                center: new this.google.maps.LatLng(lat, lng),
                zoom: 10.6,
                mapTypeControl: false,
                scaleControl: false,
                streetViewControl: false,
                rotateControl: false,
                fullscreenControl: false,
                scrollwheel: false,
                mapTypeId: this.google.maps.MapTypeId.ROADMAP,
                styles: this.map_styles,
                text: 'off',
                zoomControl: false,
            }
            this.zoom = 10.6
            this.map = new this.google.maps.Map(this.$refs.googleMainMap, mapProp)
            this.restart_icon()
            this.map_start = false
        },
        clean_map () {
            this.map = null
        },
        Zoom_out () {
            if (+this.zoom === 1) {
                return
            }
            this.map.setZoom(this.zoom - 1)
            this.zoom = this.zoom - 1
            this.restart_icon()
        },
        restart_icon () {
            if (this.centerMarker1) {
                this.centerMarker1.setMap(null)
            }
            const centerIcon = {
                url: require('@/assets/icons/google-map-dot.svg'),
                size: new this.google.maps.Size(64, 78),
                scaledSize: new this.google.maps.Size(32, 39),
                origin: new this.google.maps.Point(0, 0),
                anchor: new this.google.maps.Point(0, 0),
            }
            this.centerMarker1 = new this.google.maps.Marker({
                position: new this.google.maps.LatLng(25.041, 121.5379),
                map: this.map,
                title: 'mackent',
                icon: centerIcon,
                optimized: false,
            })
        },
        Zoom_in () {
            if (+this.zoom === 22) {
                return
            }
            this.map.setZoom(this.zoom + 1)
            this.zoom = this.zoom + 1
            this.restart_icon()
        },
        resize () {
            clearTimeout(this.mapResizeTimer)
            this.mapResizeTimer = setTimeout(() => {
                this.start_map()
            }, 200)
        },
    },
})

